.header {
	background:rgba(255, 255, 255, 0.856);
	align-items: center;
	height: 65px;
	padding: 0;
}

.header_Row{
	line-height: 35px;
	width: 100%;
	background:rgba(255, 255, 255, 0.856);
}

.appHeader_logo {
	width: 150px;
	min-width: 150px;
	height: 35px;
	background: rgba(255, 255, 255, 0.856);
	float: left;
	line-height: 0px;
}

.header_menu {

	justify-content: center;
	font-size: 16px;
	color: #3d3d3d;
		:global(.ant-menu-item-selected) {
			font-weight: 700;
	} 

	/* Selected item */
	:global(.ant-menu-item-selected:after) {
		color: #46a358 !important; 
		border-bottom-width: 3px !important; 
		border-bottom-color: #46a358 !important;
	}

	/*  mouse hover element*/
	:global(.ant-menu-item-active:after) {
		border-bottom-width: 3px !important; 
		border-bottom-color: #46a358 !important;
	}
}

.loginBlock{
	display: flex;
	float: right;
	gap: 2em;
	align-items: center;
}

.basket_img{
	line-height: 0.5em;
	width: 1.5em;
	min-width: 24px;
	height: 1.5em;
	min-height: 24px;
}

.header_button {
	display: flex;
	align-items: center;
	font-family: 'CeraPro';
	font-weight: 500;
	background: #46a358 ;
	border-radius: 6px;
	width: 100px;
	min-width: 100px;
	height: 35px;
	min-height: 35px;
	color: #fff;
	gap: 0.3em;
}


/* Mobile sm = 900------------------------------------------------------------- */

.AppHeaderMobileMenuButton{
	min-width: 65px;
	width: 65px;
	min-height: 65px;
	border-radius: 220px;
}

.AppHeaderMobileButton {
	margin: -53px 0 0 0 ;
}

.headerMobile {
	position: fixed;
	bottom: 0;
	right: 0;
	width: 100%;
	z-index: 100;

	width: 100%;
	min-width: 100%;
	height: 95px;
	min-height: 95px;

	display: flex;
	align-items: center;
	justify-content: space-around;

	background: rgb(255, 255, 255);
	box-shadow: 0px -10px 30px 0px rgba(184, 184, 184, 0.2);
	border-radius: 29px;
}







